.header {
  background-color: rgb(219, 4, 4);
  min-height: 60px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__left {
  display: flex;
}
.header__logo {
  object-fit: contain;
  max-width: 60px;
}
.header__title {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  color: white;
}
.header__right {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
}
.header__icons {
  margin: auto;
  margin-right: 20px;
}
.header__icon {
  position: relative;
  right: 0;
  color: white !important;
  margin-top: 5px;
  padding-left: 7px;
  padding-right: 7px;
}

@media screen and (max-width: 420px) {
  .header__right {
    display: none;
  }
}
