@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
* {
  margin: 0;
  padding: 0px;
  font-family: "Titillium Web", sans-serif;
  -ms-overflow-style: none;
  scrollbar-width: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
*::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html,
body {
  height: 100%;
}

html {
    height: 100%;
  }
  body {
    min-height: 100%;
  }
.homePage{
  height: 90vh;
}
.homePage__container {
  z-index: 100;
  height: 100%;
}
.homePage__image {
  position: absolute;
  top: 0;
  width: 100%;
  max-height: 90%;
  z-index: -1;
  object-fit: cover;
  overflow: auto;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}
.header {
  background-color: rgb(219, 4, 4);
  min-height: 60px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header__left {
  display: flex;
}
.header__logo {
  object-fit: contain;
  max-width: 60px;
}
.header__title {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  color: white;
}
.header__right {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
}
.header__icons {
  margin: auto;
  margin-right: 20px;
}
.header__icon {
  position: relative;
  right: 0;
  color: white !important;
  margin-top: 5px;
  padding-left: 7px;
  padding-right: 7px;
}

@media screen and (max-width: 420px) {
  .header__right {
    display: none;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
}

.navitem {
  margin-top: auto;
  margin-bottom: auto;
  padding: 10px;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: rgb(221, 221, 221);
}

.body{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
    align-items: center;
}
.body__landing{
    margin: 4%;
}
.landing {
  display: flex;
  flex-direction: column;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background-image: linear-gradient(
    to bottom,
    rgba(255, 0, 0, 0.1),
    rgba(50, 137, 196, 0.1)
  );
  border-radius: 10px;
  padding: 30px;
  margin: auto;
}

.cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.card__action {
  position: relative;
  bottom: 0px;
  max-width: 150px;
  border-radius: 50px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
  background-color: #fffefe !important;
  font-weight: bold !important;
}
.card {
  display: flex;
  flex-direction: column;
  padding: 20px;
  flex: 0.3 1;
  flex-grow: 1;
  min-width: 300px;
  margin: 10px;
  border-radius: 10px;
  background-color: #baf7f8;
  transition: all 0.2s ease-in-out;
  padding-bottom: 40px;
  justify-content: space-evenly;
}
.card:hover {
  transform: scale(1.1);
  background-color: #d4fefd;
}
.card__headline {
  color: #3289c4;
  margin-bottom: 10px;
}

.card__description {
  margin-bottom: 30px;
}

.card__title {
  margin-bottom: 10px;
}

.appointment__bg {
  background-color: rgb(160, 0, 0);
  z-index: -1;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  position: absolute;
}
.appointment {
  margin: auto;
}
.appointment__body {
  display: flex;
  z-index: 100;
  flex-direction: column;
  max-width: 600px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
}

label {
  padding: 10px;
}
input {
  padding: 10px;
  margin: 5px;
}

.apForm__body {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 7px;
  padding-bottom: 40px;
  margin: 10px;
}
.apForm__body > form {
  display: flex;
  flex-direction: column;
}

.apForm__name {
  flex: 0.2 1;
  display: flex;
  flex-direction: column;
}
.apForm__nameInput {
  display: flex;
  justify-content: space-evenly;
}
.apForm__email {
  flex: 0.2 1;
  display: flex;
  flex-direction: column;
}
.apForm__tel {
  flex: 0.2 1;
  display: flex;
  flex-direction: column;
}
.apForm__button > Button {
  flex: 0.2 1;
  margin-top: 20px;
  width: 100%;
  padding: 10px;
  background-color: red !important;
}

